<script>
import Bus from "../../../../utils/Bus";

export default {
  name: "ClientManagement.vue",
  data() {
    let lau_guest_phone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('手机号不能为空'))
      } else if (!this.isCellPhone(value)) {
        callback(new Error('请输入正确的手机号!'))
      } else {
        callback()
      }
    };
    return {
      row_disabled: '',
      row_lpi_uuid: '',
      ruleForm: {
        lau_guest_phone: '',
      },
      cardBodyStyle: 'padding-top:10px;padding-bottom:10px;',
      icon1: 'el-icon-caret-top',
      cardBodyHeight: true,
      tableData: [],//表格数据
      sortOrderCol: '',
      /** 查询参数 */
      queryParam: {
        totalRow: 0,
        currentPage: 1,
        pageSize: this.customGlobal.pageSize,
      },
      /** 是否显示隐藏窗口 */
      platformRoleFormVisible: false,
      tableDialgTitle: '',//新增，修改
      giftForm: {},
      giftFormrule: {},
      tableData_prize: [],
      giftshow: false,
      tableDialgTitle1: '',
      gifttypeForm: {
        lpi_caption: '',
        lub_prise_uuid: '',
        lub_prise_pool_uuid: '',
        prize_id_name: ''
      },
      gifttyperule: {},
      ruleFormrule: {
        lau_guest_phone: [
          {required: true, validator: lau_guest_phone, trigger: 'blur'}
        ],
      },
      tableData_type: [],
      button: '',
      lub_uuid: '',
      lai_detail_status:''
    }
  },
  created: function () {
    this.queryParam.currentPage = 1;
    this.query_name();
    console.log("参数：" + this.getQqueryParams(this.pri_detail.info.jsonobj.url).lau_aci_uuid);
    console.log("参数：" + this.getQqueryParams(this.pri_detail.info.jsonobj.url).type);
    console.log("参数：" + this.getQqueryParams(this.pri_detail.info.jsonobj.url).lai_caption);
    console.log("参数：" + this.getQqueryParams(this.pri_detail.info.jsonobj.url).lai_detail_status);
    this.lai_detail_status = this.getQqueryParams(this.pri_detail.info.jsonobj.url).lai_detail_status;
    console.log("this.lai_detail_status:"+this.lai_detail_status)
  },
  props: {
    //获取url参数
    pri_detail: {
      type: Object,
      default: function () {
        return {
          info: {}
        }

      }
    }
  },
  methods: {
    getQqueryParams(url) {
      let params = {};
      if (url) {
        let queryArray = url.split('?');
        if (queryArray.length > 1) {
          let query = queryArray[1];
          let array = query.split('&');
          array.map((value) => {
            let valueArray = value.split('=');
            if (valueArray.length > 1) {
              // 还需要对value进行解码（可能涉及到在value为中文字符，在赋值到二维码前进行了encodeURI编码）
              // Object.assign(params, { [valueArray[0]]: decodeURI(valueArray[1]) })
              Object.assign(params, {[valueArray[0]]: valueArray[1]});

            }
          })
        }
      }
      return params;
    },
    /** 电话号校验 */
    isCellPhone(val) {
      if (!/^1(3|4|5|6|7|8)\d{9}$/.test(val)) {
        return false
      } else {
        return true
      }
    },
    /** 设置查询框隐藏显示  */
    confCardBodyHeigth() {
      if (this.cardBodyHeight === false) {
        this.cardBodyHeight = true;
        this.cardBodyStyle = 'padding-top:10px;padding-bottom:10px;';
        this.icon1 = 'el-icon-caret-top';
      } else {
        this.cardBodyHeight = false;
        this.cardBodyStyle = 'padding-top:0px;padding-bottom:0px;';
        this.icon1 = 'el-icon-caret-bottom';
      }
      //  console.info("confCardBodyHeigth:"+this.cardBodyHeight);
    },
    //重置
    clearQuery(formName) {
      this.$refs[formName].resetFields();
    },
    //查询
    query_name() {
      let that = this;
      let _jsondata_ = {
        lub_aci_uuid: that.getQqueryParams(this.pri_detail.info.jsonobj.url).lau_aci_uuid,
        lub_guest_phone: that.ruleForm.lau_guest_phone,
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        perPageSize: that.queryParam.pageSize,
        currentPage: that.queryParam.currentPage,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.select_phone_gift,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status == 'success') {
          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;

        } else {
          console.info("手机号查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '手机号查询出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '手机号查询出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //新增
    platformAddRole(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let that = this;
          let _jsondata_ = {
            lau_aci_uuid: that.getQqueryParams(this.pri_detail.info.jsonobj.url).lau_aci_uuid,
            lau_guest_phone: that.ruleForm.lau_guest_phone,
            lau_cui_id: that.customGlobal.userInfoExt.cui_uuid,
            lau_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
            lau_province_id: that.customGlobal.userInfoExt.cui_province_id,
            lau_city_id: that.customGlobal.userInfoExt.cui_city_id,
            lau_county_id: that.customGlobal.userInfoExt.cui_county_id,
            lau_station_id: that.customGlobal.userInfoExt.cui_station_id,
          };
          const loading = this.$loading({
            lock: true,
            text: '提交中...',
            target: '.main',
            spinner: 'el-icon-loading',

            background: 'rgba(0, 0, 0, 0)'
          });
          var a1 = this.customGlobal.postHttpClient(
            this.customGlobal.serverUrl.Add_phone_gift,
            //this.customGlobal.serverUrl.baseURI,
            this.customGlobal.serverUrl.orgBaseURI,
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
          );
          a1.then((res) => {
            if (res.data.status == 'success') {
              loading.close();
              this.$message({
                type: 'success',
                showClose: true,
                duration: 3000,
                dangerouslyUseHTMLString: true,
                message: '<span style="color:black;">新增手机号成功 </span>'
              });
              that.ruleForm.lau_guest_phone = '';
              this.queryParam.currentPage = 1;
              this.query_name();
            } else {
              console.info("新增手机号失败");
              loading.close();
